-->
<template>
  <div>
    <!--    <svg-icon :icon-class="isFullscreen?'exit-fullscreen':'fullscreen'" @click="click" />-->
    <img
      v-show="isFullscreen"
      class="fullscreenBox"
      width="24px"
      height="24px"
      src="@/assets/img/fullscreen.png"
      @click="click"
      title="还原"
    />
    <img
      v-show="!isFullscreen"
      class="fullscreenBox"
      width="24px"
      height="24px"
      src="@/assets/img/exit-fullscreen.png"
      @click="click"
      title="全屏"
    />
  </div>
</template>

<script>
import screenfull from "screenfull";

export default {
  name: "Screenfull",
  data() {
    return {
      isFullscreen: false,
    };
  },
  mounted() {
    this.init();
  },
  beforeDestroy() {
    this.destroy();
  },
  methods: {
    click() {
      if (!screenfull.isEnabled) {
        this.$message({ message: "你的浏览器不支持全屏", type: "warning" });
        return false;
      }
      screenfull.toggle();
    },
    change() {
      this.isFullscreen = screenfull.isFullscreen;
    },
    init() {
      if (screenfull.isEnabled) {
        screenfull.on("change", this.change);
      }
    },
    destroy() {
      if (screenfull.isEnabled) {
        screenfull.off("change", this.change);
      }
    },
  },
};
</script>

<style scoped>
.screenfull-svg {
  display: inline-block;
  cursor: pointer;
  fill: #5a5e66;
  width: 20px;
  height: 20px;
  vertical-align: 10px;
}
.fullscreenBox {
  cursor: pointer;
}
</style>
