<!--
 * @Description: 遍历functionBox.json 获取所有的功能
 * @Author: mhf
 * @Date: 2023-04-06 23:12:19
-->
<template>
  <div class="home">
    <!-- 页面头部的背景 -->
    <div class="home-top"></div>

    <!-- 主体 -->
    <div class="flex-box">
      <div style="display: flex; align-items: center">
        <h3 style="color: #009a61">Vue项目工具集</h3>

        <div class="flex-box-screen">
          <ScreenFull />
        </div>
      </div>

      <!-- 遍历得到的功能盒子 -->

      <div v-for="(item, index) in functionBox" class="normal-box" :key="index">
        <el-button
          class="normal-box-button"
          type="text"
          @click="routerGo(item.goRouter)"
          :title="item.text"
          >{{ index + 1 }}、{{ item.text }}
        </el-button>
        <div class="normal-box-intro" :title="item.intro">{{ item.intro }}</div>
        <div class="normal-box-consult">
          <a
            v-for="(item1, index1) in item.consult"
            :key="index1"
            :href="item1.link"
            target="_blank"
            >{{ item1.name }}</a
          >
        </div>
        <div class="normal-box-done">
          <img
            :src="
              item.pic === 'donePic'
                ? donePic
                : item.pic === 'editPic'
                ? editPic
                : willAddBackToEndPic
            "
            alt=""
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ScreenFull from "@/components/Screenfull";

export default {
  name: "home",
  components: { ScreenFull },
  data() {
    return {
      functionBox: [],
      donePic: require("../../public/img/done.png"),
      editPic: require("../../public/img/edit.png"),
      willAddBackToEndPic: require("../../public/img/willAddBackToEnd.png"),
    };
  },
  computed: {},
  methods: {
    /**
     * @Interface 接口
     * @description: 模拟数据请求（请求本地JSON文件）
     * */
    getFunctionBox() {
      this.$axios
        .create({
          baseURL: "",
          timeout: 5000,
          headers: { "Content-type": "multipart/form-data" },
        })
        .get("/functionBox.json")
        .then((res) => {
          if (res.data.code === 200) {
            this.functionBox = res.data.data;
          }
        });
    },

    /**
     * @Event 方法
     * @description: 点击标题 跳转到对应的页面
     * */
    routerGo(data) {
      this.$router.push(data);
    },
  },

  created() {
    this.getFunctionBox();
  },
};
</script>

