/**
 * @Event 方法
 * @description: 引入 fontawesome图标库
 * */

import { library } from "@fortawesome/fontawesome-svg-core"; // fontawesome core 图标
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome"; // fontawesome 组件
/* import all icons, solid图标库的包名为fas、regular图标库的包名为far、brands图标库的包名为fab */
import { fas } from "@fortawesome/free-solid-svg-icons";
import { far } from "@fortawesome/free-regular-svg-icons";
import { fab } from "@fortawesome/free-brands-svg-icons";
/* add icons to the library */
library.add(fas, far, fab);
/* add font awesome icon component */

import Vue from "vue";

Vue.component("font-awesome-icon", FontAwesomeIcon);
