<template>
  <el-dialog
    title="uploadFile"
    :visible.sync="dialogVisible"
    width="60%"
    :append-to-body="true"
    :modal-append-to-body="false"
    :before-close="closeDialog"
  >
    <div class="upload-file-box">
      <el-upload
        :action="fileUrl"
        :headers="headers"
        :before-upload="beforeUpload"
        :on-preview="handlePreview"
        :on-remove="handleRemove"
        :on-success="handleSuccess"
        :before-remove="beforeRemove"
        multiple
        :limit="5"
        :on-exceed="handleExceed"
        :file-list="fileList"
      >
        <el-button size="small" type="primary" icon="el-icon-plus"
          >上传文件</el-button
        >
        <!-- <div slot="tip" class="el-upload__tip">可上传图片、Excel、Word、pdf格式文件</div> -->
      </el-upload>
    </div>

    <!-- 附件预览的弹窗 -->
    <div>
      <el-dialog
        title="附件预览"
        :visible.sync="dialogVisible1"
        width="70%"
        :before-close="closeDialog1"
        :modal-append-to-body="false"
        :append-to-body="true"
      >
        <iframe style="width: 100%; height: 600px" :src="pathFile"></iframe>
        <div slot="footer" class="dialog-footer">
          <el-button
            class="filter_button"
            type="primary"
            @click="dialogVisible1 = false"
            >关 闭</el-button
          >
        </div>
      </el-dialog>
    </div>

    <span slot="footer" class="dialog-footer">
      <el-button @click="closeDialog">关 闭</el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  name: "fileUpload",
  data() {
    return {
      dialogVisible: false,
      dialogVisible1: false,
      // fileUrl: 'https://kpgl.yjj.sx.gov.cn:8866/v1/base/picture/upload', // 图片上传的接口
      fileUrl: "https://kpgl.yjj.sx.gov.cn:8867/api/file/upload", // 文件上传的接口

      /* 获取 Authorization */
      /* 登录地址：https://kpgl.yjj.sx.gov.cn:8866/sxkaopei/ */
      /* 账号：shaoxing */
      /* 密码：Sxs.@123 */
      /* 二级密码：sxadmin2021 */

      headers: { Authorization: "ae5a662fc3d84e3680fa63c1ae32e57a" },
      pathFile: "",
      fileUrlLoad: "https://kpgl.yjj.sx.gov.cn:8867/file", // 文件回显的接口
      fileList: [],
      formData: {
        enclosureFiles: [],
      },
    };
  },
  methods: {
    showDialog() {
      this.dialogVisible = true;
    },
    closeDialog() {
      this.dialogVisible = false;
    },
    closeDialog1() {
      this.dialogVisible1 = false;
    },

    /* 文件上传 */
    beforeUpload(file) {
      // const success = uploadType(file, 'fileAndImage');
      // if (!success) {
      //     this.$message.warning('上传文件只能是 图片（jpg/png）/pdf/word/excel 格式!');
      // }
      // return success;
    },
    handlePreview(file) {
      console.log(file);
      this.dialogVisible1 = true;
      if (file.url) {
        this.pathFile = this.fileUrlLoad + file.url;
      }
      if (file.path) {
        this.pathFile = this.fileUrlLoad + file.path;
      } else {
        this.pathFile = this.fileUrlLoad + file.response.data.path;
      }
    },
    handleRemove(file, fileList) {
      const that = this;
      this.formData.enclosureFiles.forEach((item, index) => {
        if (file.response) {
          if (item.id === file.response.data.id) {
            this.formData.enclosureFiles.splice(index, 1);
          }
        } else {
          if (item.id === file.id) {
            this.formData.enclosureFiles.splice(index, 1);
          }
        }
      });
    },
    handleSuccess(response, file, fileList) {
      // console.log(response);
      this.formData.enclosureFiles.push({
        uid: file.uid,
        id: response.data.id,
      });
    },
    beforeRemove(file, fileList) {
      // return this.$confirm(`确定移除 ${file.name}？`);
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 5 个文件，本次选择了 ${files.length} 个文件，共选择了 ${
          files.length + fileList.length
        } 个文件`
      );
    },
  },
};
</script>

